.thanks-page
	+dark-header
	.thanks-head-wrap
		padding-top: 125px
		padding-bottom: 0
		.container
			padding-top: 150px
			padding-bottom: 150px
			background-repeat: no-repeat
			background-image: url('../images/thanks-page-waves-bg.png')
			background-position: left center
		.section-head,
		.section-sub-head
			font-size: 50px
			line-height: 95px
		.section-sub-head
			color: #cccc99
	.head
		margin-bottom: 30px
		font-size: 24px
		line-height: 28px
		font-weight: bold
		color: color(text)
	.menu
		list-style: none
		margin: 0
		padding: 0
		display: flex
		justify-content: space-around
		flex-wrap: wrap
		li
			width: 50%
			font-size: 18px
			line-height: 16px
			color: color(sub-text)
			&:hover
				color: color(bg)
			&:not(:last-child)
				margin-bottom: 15px
