.action-page
	+dark-header
	section.content
		padding: 0 0 150px
		background: url('../images/action-content-bg-1.png') top right, url('../images/action-content-bg-2.png') left center, url('../images/action-content-bg-3.png') bottom right
		background-repeat: no-repeat
		.row
			&:not(:last-child)
				margin-bottom: 150px
		p
			font-size: 18px
			line-height: 32px
			&:not(:last-child)
				margin-bottom: 32px
	.content-wrap
		padding: 0
		.section-head
			font-size: 50px
			line-height: 58px
		.section-head,
		.section-sub-head
			margin-bottom: 45px
	.action
		&-full-name
			margin-bottom: 87px
		&-description
			>*
				display: flex
				align-items: center
				margin-bottom: 24px
				img
					min-width: 40px
					max-width: 40px
					height: 40px
					margin-right: 14px
				p
					font-size: 24px
					line-height: 28px
					color: color(sub-text)
		&-short-description
			margin-top: 26px
			margin-bottom: 35px
			p.head
				font-size: 26px
				line-height: 30px
				font-weight: bold
				margin-bottom: 14px
			p
				margin-bottom: 0
		&-subscribe-places
			color: color(green)
			font-size: 26px
			line-height: 30px
			margin-bottom: 68px
	.main-content
		img
			margin-right: 25px
