.flex
	display: flex
	flex-wrap: wrap

ul.breadcrumbs
	font-family: 'Trebuchet MS', 'Roboto', sans-serif
	margin: 0
	padding: 0
	color: color(green)
	list-style: none
	display: flex
	flex-wrap: wrap
	font-size: 18px
	line-height: 21px
	li
		padding-right: 10px
		&:first-child
			padding-left: 0
		&:last-child
			padding: 0
			border-bottom: 1px solid color(green)
		&:not(:last-child)
			position: relative
			margin-right: 10px
			&:after
				content: ''
				position: absolute
				top: 50%
				right: 0
				transform: translateY(-50%)
				width: 1px
				height: 13px
				background-color: color(green)
		a
			transition: .5s
			&:hover
				color: color(bg)

.pagination
	&.center
		ul
			justify-content: center
	&.right
		ul
			justify-content: flex-end
	ul
		padding: 0
		margin: 0
		list-style: none
		display: flex
		li
			&:not(:last-child)
				margin-right: 10px
		.page-number
			display: block
			&.active,
			&.current,
			&.dots
				cursor: default
				pointer-events: none

.text
	@each $value in (center, left, right, justify)
		&-#{$value}
			text-align: $value



$justify-content: (center, space-between, space-around, flex-start, flex-end)
$align-items: (center, baseline, flex-start, flex-end)

$row-align: (justify-content: $justify-content, align-items: $align-items)
@each $name, $value in $row-align
	@each $value-name in $value
		.#{$name}-#{$value-name}
			#{$name}: #{$value-name}
			display: flex
			flex-wrap: wrap
