footer
	border-top: 4px solid color(green)
	background-image: url('../images/footer-waves-bg.png')
	background-repeat: no-repeat
	background-position: right top
	background-size: contain
	padding: 100px 0 150px
	background-color: #262323
	.links
		list-style: none
		padding-left: 0 !important
		li
			&:not(:last-child)
				margin-bottom: 20px
		a
			font-size: 24px
			line-height: 16px
			color: #ffffff !important
			&.router-link-active,
			&:hover
				color: color(bg) !important
	.social
		display: flex
		align-self: flex-end
		a
			font-size: 23px
			color: #B9C7A1 !important
			&:not(:last-child)
				margin-right: 42px
			&:hover
				color: color(green) !important
	.contacts
		&-list
			&-item
				position: relative
				padding-left: 30px
				margin-bottom: 20px

				img
					position: absolute
					top: 6px
					left: 0
					width: 20px
					height: 20px

				&-text
					color: color(sub-text)
					font-size: 18px
					line-height: 32px

					> *
						display: block
						width: fit-content

					a:hover
						color: color(green) !important
