.team
    background-color: #B9C7A1
    background-image: url('../images/team-waves-bg.png')
    background-repeat: no-repeat
    background-position: right top
    .section-head
        margin-bottom: 80px
    &-slider
        .slick-arrow
            font-size: 50px
            transition: .5s
            &:nth-of-type(1)
                left: -80px
            &:nth-of-type(2)
                right: -80px
            &:hover
                color: color(green)
    &-slide
        background-color: #ffffff
        padding: 45px 29px
        margin: 0 15px
        .btn
            width: 100%
    &-member
        &-name
            color: color(green)
            margin-bottom: 20px
            font-size: 36px
            line-height: 42px
        &-img
            display: block
            width: 100%
            max-height: 347px
            margin-bottom: 27px
            &-wrap
                position: relative
        &-icon
            position: absolute
            top: 0
            right: 13px
            transform: translateY(-50%)
            z-index: 1
            width: fit-content
            padding: 12px
            border-radius: 16px
            background-color: #ffffff
            box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1)
            img
                display: block
                width: 40px
                height: 40px
        &-head
            font-size: 30px
            line-height: 35px
            color: color(text)
            margin-bottom: 45px
        &-work-place
            font-size: 18px
            line-height: 21px
            color: color(sub-text)
            margin-bottom: 37px
        &-specialization-description
            margin-bottom: 20px
        &-services-description
            ul
                margin: 0
                padding-left: 24px
