.input-group
    $this: &
    position: relative
    margin-bottom: 30px

    &__input
        padding: 8px 0
        font-size: 16px
        background-color: transparent
        border: 0
        border-bottom: 1px solid #90D21F
        width: 100%

    &__select
        padding: 8px 25px 8px 5px
        font-size: 16px
        background-color: transparent
        border: 0
        border-bottom: 1px solid #90D21F
        width: 100%
        appearance: none
        position: relative
        background:
            image: url("data:image/svg+xml,%3Csvg%20fill%3D%22%2390D21F%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20viewBox%3D%220%200%20256%20512%22%20width%3D%2218%22%20height%3D%2218%22%3E%3Cpath%20d%3D%22M119.5%20326.9L3.5%20209.1c-4.7-4.7-4.7-12.3%200-17l7.1-7.1c4.7-4.7%2012.3-4.7%2017%200L128%20287.3l100.4-102.2c4.7-4.7%2012.3-4.7%2017%200l7.1%207.1c4.7%204.7%204.7%2012.3%200%2017L136.5%20327c-4.7%204.6-12.3%204.6-17-.1z%22%2F%3E%3C%2Fsvg%3E")
            position: right 5px top 50%
            repeat: no-repeat

        &:invalid
            color: #90d21f
        option
            &:not([disabled])
                color: #000

    &__placeholder
        position: absolute
        top: 50%
        left: 0
        pointer-events: none
        transition: 250ms
        font-size: 18px
        transform: translateY(-50%)
        transform-origin: top left
        color: #90D21F
        background-color: #fff

        #{$this}.is-active &
            top: 0
            transform: translateY(-50%) scale(.75)
