.news-list-page
    .breadcrumbs
        padding-bottom: 50px

    .section-head
        margin-bottom: 87px

.news-list
    &.before
        padding-top: 0
        background-image: url('../images/news-waves-bg-1.png')
        background-repeat: no-repeat
        background-position: top left
    [class^=col-]
        margin-bottom: 20px
    &.after
        background-image: url('../images/news-waves-bg-2.png')
        background-repeat: no-repeat
        background-position: bottom right
        .grid-wrap
            display: grid
            grid-template-columns: 32% 32% 32%
            grid-template-rows: 25% 25% 25% 25%
            grid-gap: 0 25px
            grid-template-areas:  "area1 area1 area2" "area1 area1 area3" "area4 area5 area6" "area7 area8 area9"
            .news
                padding: 0
                height: 100%
                margin-bottom: 0
                &-name
                    left: 0
                    right: 0

            $i: 1
            $grid-news: (area1, area2, area3, area4, area5, area6, area7, area8, area9)
            @while $i <= 9
                .news-grid-#{$i}
                    grid-area: nth($grid-news, $i)
                    padding-bottom: 20px
                $i: $i + 1

    .load-more
        display: flex
        justify-content: center
        align-items: center
        width: 100%
        margin-top: 100px
        padding: 0
        text-align: center
        font-size: 36px
        line-height: 42px
        font-weight: bold
        i
            color: color(green)
            margin-right: 30px
            font-size: 100px

    .news-subscribe
        height: 100%

    .news
        position: relative
        padding: 0 15px
        margin-bottom: 20px
        transition: .5s

        .play
            position: absolute
            top: 50%
            left: 50%
            transform: translate(-50%, -50%)
            cursor: pointer
            font-size: 83px
            color: #ffffff
            z-index: 2

        iframe
            width: 100%
            height: 100%
            min-height: 334px
            display: block
            transition: .5s

        &-wrap
            cursor: pointer
            height: 100%
            position: relative

        &-name
            position: absolute
            top: 0
            left: 0
            right: 0
            padding: 38px
            color: #ffffff
            background-color: rgba(#262323, 0.75)
            z-index: 1
            font-size: 22px
            line-height: 26px
            display: block
        &-picture
            aspect-ratio: 16/9
            object-fit: cover
            width: 100%
            height: auto
        &-info
            position: absolute
            bottom: 8px
            left: 0
            right: 0
            display: flex
            justify-content: space-between
            padding: 14px 20px
            background-color: rgba(255, 255, 255, 0.6)
            font-size: 16px
            z-index: 1
            p
                margin-bottom: 0
        &-category
            position: absolute
            left: 0
            bottom: 77px
            padding: 14px 49px 14px 19px
            color: color(bg)
            background-color: #ffffff
            box-shadow: 0 2px 40px rgba(0, 0, 0, 0.1)
