section
	&.actions
		background-color: #B9C7A1
		background-image: url('../images/home/actions-waves-bg.png')
		background-repeat: no-repeat
		background-position: right center
		background-size: contain
		padding: 0
		.section-head
			margin-bottom: 68px

		.actions-slider
			.slick-dots
				padding-top: 20px
				li
					button
						background-color: color(bg)
					&.slick-active
						button
							background-color: #fff
		.actions
			&-slider-wrap
				padding-top: 152px
				padding-bottom: 128px
			&-active-slider
				height: 100%
				.slick-list,
				.slick-track
					height: 100%
				.actions
					&-slide
						max-width: unset
						padding-right: 0
						display: flex
						flex-direction: column
						&-img
							&-wrap
								height: 582px
								.btn
									height: unset
									width: auto
									i
										margin-left: 10px
						&-description
							height: 100%
							padding-top: 56px
							padding-bottom: 25px
							//height: 312px
							display: flex
							flex-direction: column
							justify-content: center
							.name
								font-size: 36px
								line-height: 42px
								margin-bottom: 28px
							.time,
							.place
								font-size: 20px
								line-height: 23px
								&:not(:last-child)
									margin-bottom: 17px

			&-slide
				padding-right: 48px
				max-width: 448px
				&-img
					width: 100%
					height: 100%
					object-fit: cover
					&-wrap
						height: 245px
						position: relative
						.btn
							position: absolute
							top: 100%
							right: 32px
							width: 48px
							height: 48px
							display: flex
							align-items: center
							justify-content: center
							transform: translateY(-50%)

				&-description
					background-color: #ffffff
					padding: 16px 32px 18px
					.name
						font-size: 26px
						line-height: 30px
						margin-bottom: 10px

					.time,
					.place
						display: flex
						align-items: center
						&:not(:last-child)
							margin-bottom: 10px
						p
							color: color(sub-text)
							font-size: 14px
						img
							width: 20px
							height: 20px
							object-fit: contain
							margin-right: 10px

.actions
	.head
		position: relative
		min-height: 100vh
		padding-top: 0
		background-color: rgba(30, 31, 32, 0.81)
		background-repeat: no-repeat
		background-image: url('../images/actions/head-bg.png')
		background-position: center center
		background-size: cover

		&:before
			content: ''
			position: absolute
			top: 0
			left: 0
			width: 100%
			height: 100%
			background-color: rgba(30, 31, 32, 0.81)
			z-index: 0

		.section-head
			margin-bottom: 47px

		.head-text
			color: #ffffff
			font-size: 18px
			line-height: 32px

		.has-idea
			width: 100%
			background-color: #ffffff
			background-image: url('../images/actions/has-idea-bg.png')
			background-repeat: no-repeat
			background-position: right center
			padding: 20px
			margin-top: 20px
			&.absolute
				position: absolute
				top: 0
				left: 0
				z-index: 1
				margin-top: 0
				padding: 60px 80px 117px

			&-head
				color: color(green)
				font-size: 48px
				line-height: 56px
				margin-bottom: 25px

			&-text
				color: color(sub-text)
				font-size: 30px
				line-height: 35px
				margin-bottom: 60px

	section.actions
		background-image: url('../images/home/waves-bg.png')
		background-repeat: no-repeat
		background-position: 0 0
		background-size: 100%
		.filter
			&-wrap
				display: flex
				flex-wrap: wrap
				align-items: center
				margin-bottom: 85px
			&-hint
				font-size: 30px
				line-height: 35px
				font-weight: bold
			&-items
				padding-left: 25px
			&-item
				font-size: 24px
				line-height: 28px
				padding: 0
				color: color(green)
				border-bottom: 1px solid color(green)
				&:not(:last-child)
					margin-right: 17px
				&.active,
				&:hover
					border-bottom-color: color(sub-text)
					color: color(sub-text)
				+active
		.action
			box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.05)
			margin-bottom: 60px

			&-img
				width: 100%
				height: 100%
				object-fit: cover

				&-wrap
					height: 245px
					position: relative

					.btn
						position: absolute
						top: 100%
						right: 14px
						display: flex
						align-items: center
						justify-content: center
						transform: translateY(-50%)
						i
							margin-left: 13px

			&-description
				background-color: #ffffff
				padding: 51px 26px 49px

				.name
					font-size: 28px
					line-height: 32px
					margin-bottom: 20px
					font-weight: bold

				.time,
				.place
					display: flex
					align-items: center

					&:not(:last-child)
						margin-bottom: 10px

					p
						color: color(sub-text)
						font-size: 14px

					img
						width: 20px
						height: 20px
						object-fit: contain
						margin-right: 10px
