.single-news-page
    section.breadcrumbs
        padding-bottom: 100px

    .news-subscribe
        margin-bottom: 40px

    .news-card
        position: relative
        margin-bottom: 20px

        &.more
            cursor: pointer

        &-name
            position: absolute
            top: 0
            left: 0
            right: 0
            padding: 38px
            color: #ffffff
            background-color: rgba(#262323, 0.75)
            z-index: 1
            font-size: 22px
            line-height: 26px

        &-picture
            height: auto
            aspect-ratio: 16/9
        &-info
            position: absolute
            bottom: 8px
            left: 0
            right: 0
            display: flex
            justify-content: space-between
            padding: 14px 20px
            background-color: rgba(255, 255, 255, 0.6)
            font-size: 16px
            z-index: 1
            p
                margin-bottom: 0
        &-category
            position: absolute
            left: 0
            bottom: 77px
            padding: 14px 49px 14px 19px
            color: color(bg)
            background-color: #ffffff
            box-shadow: 0 2px 40px rgba(0, 0, 0, 0.1)

    .news-content
        p
            font-size: 18px
            line-height: 32px

            &:not(:last-child)
                margin-bottom: 32px

        a
            color: color(green) !important
            text-decoration: underline transparent
            transition: .5s

            &:hover
                text-decoration: underline color(green)

        img
            margin-right: 25px
