@media (max-width: 1347px)
	.section-head
		font-size: 60px
		line-height: normal

@media (max-width: 1264px)
	.locations-page .head .location-images-slider img
		max-height: 300px

@media (min-width: 1200px) and (max-width: 1400px)
	.actions .head .has-idea.absolute
		padding: 53px 25px 53px 50px

@media (max-width: 1200px)
	header
		padding: 25px 0
		ul
			li
				> span
					display: none
				ul
					position: static
					opacity: 1
					visibility: visible
					transform: none
					padding: 0
					li
						background-color: transparent
						min-width: unset
						text-align: left
						padding: 0
						a
							padding: 0
	.main
		.about
			&-slide
				img
					width: 100%
					max-width: 500px !important
					margin-left: auto
					margin-right: auto
	.team-slider
		.slick-arrow
			&:nth-of-type(1)
				left: -15px

			&:nth-of-type(2)
				right: -15px
	.team-slide
		margin: 0

		.btn
			width: fit-content
			margin: 0 auto 20px
			padding-left: 30px
			padding-right: 30px

@media (max-width: 1199px)
	.section-head.text-right
		text-align: left
	section,
	footer,
	.about section.history
		padding: 30px 0
	.main
		section
			&.services
				padding-bottom: 20px

			&.locations
				padding-top: 20px

			&.locations
				.locations
					&-image
						&-slide
							width: 100%
							max-width: 500px !important
							margin: 0 auto 20px

		.head-slide
			padding-top: 120px
			padding-bottom: 250px
			min-height: unset
			align-items: flex-start
	section.breadcrumbs
		padding-top: 150px
		padding-bottom: 30px
	.actions
		.head
			min-height: unset

			.has-idea
				&-head
					font-size: 35px
					line-height: normal

				&-text
					font-size: 20px
					margin-bottom: 30px
	.action-page
		section.content
			.row:not(:last-child)
				margin-bottom: 60px

		.main-image
			width: 100%
			max-height: 300px
			object-fit: cover
			margin-bottom: 20px
	.thanks-page
		.thanks-head-wrap
			padding-top: 96px

			.container
				padding-top: 50px
				padding-bottom: 50px
				background-image: none
	.not-found-wrap
		padding-top: 96px

		.section-head
			font-size: 200px
			line-height: normal
			margin-bottom: 30px

		.section-sub-head
			margin-bottom: 30px


@media (max-width: 1024px)
	.main
		section.services
			.service
				&:hover
					.service
						&-img
							height: auto

							&-wrap:before
								display: none

						&-name
							transform: none
							color: color(text)
							margin-bottom: 0

						&-icon
							opacity: 1

						&-description
							padding-top: 48px
							padding-bottom: 48px

				&-img
					height: auto

				&-description
					padding-right: 10px
					padding-left: 10px

					&-wrap
						height: 207px
	.action-page
		.action-description
			> *
				p
					font-size: 20px
					line-height: normal

		.action-subscribe-places
			margin-bottom: 30px

@media (max-width: 991px)
	.main
		.with-support
			&-slider
				height: auto
				justify-content: space-between

				&-wrap
					p
						margin-bottom: 10px

					img
						width: 48%
						margin-bottom: 10px
						object-fit: scale-down
						max-height: 64px

						&:nth-last-child(-n+2)
							margin-bottom: 0

		.head-slide
			padding-bottom: 270px

			&-head
				font-size: 50px
				line-height: normal

			&-sub-head
				font-size: 30px
				line-height: normal
	.contacts-section-wrap
		.section-head
			margin-bottom: 35px

		.contacts-wrap
			padding: 50px 20px 40px
			background-position: 5px 14px
			border-radius: 5px

			.social
				margin-top: 20px
				margin-bottom: 30px
	.menu-container
		.menu
			flex-direction: column
			position: absolute
			top: 100%
			left: 0
			width: 100%
			display: none

			&-item
				&-has-children
					.sub-menu
						margin-top: 10px

					li
						padding-left: 10px

					.menu-item-has-children
						&:hover
							&:before
								border-color: #000
								transform: rotateZ(135deg)
	.actions
		section.actions
			.filter
				&-wrap
					margin-bottom: 35px
	.action-page
		section.content
			.row:not(:last-child)
				margin-bottom: 30px

		.section-head
			line-height: normal

		.action-full-name
			margin-bottom: 30px
	.service-page
		.head
			.section-head
				margin-bottom: 30px

		.services-head
			.container
				padding-top: 30px !important
				padding-bottom: 32px !important

		.services-list
			.service-img
				max-height: 200px

			.service-wrap
				.btn
					width: fit-content
					margin-left: auto
					margin-right: auto
					padding-left: 30px
					padding-right: 30px
					margin-bottom: 20px
	.not-found-wrap
		ul.menu
			margin-bottom: 30px
	.thanks-page
		.thanks-head-wrap
			.section-head,
			.section-sub-head
				font-size: 35px
				line-height: normal
				margin-bottom: 10px
	footer
		background-size: cover
		.links
			margin-bottom: 30px
			text-align: center

@media (max-width: 959px)
	.single-news-page
		.news-card
			.v-responsive.v-image
				height: auto !important
				max-height: 334px

			&-name
				position: unset
				padding: 15px
				background-color: #262323
	.news-list-page
		.news-list
			.load-more
				margin-top: 20px
				font-size: 22px
				line-height: 24px

				i
					font-size: 60px

			.news
				.v-responsive.v-image
					height: auto !important
					max-height: 334px

				&-name
					position: unset
					padding: 15px
					background-color: #262323

@media (max-width: 768px)
	.main
		section
			&.locations
				.section-head
					margin-bottom: 20px

		.about-slider
			margin-top: 20px
	.actions
		section.actions
			.action
				margin-bottom: 35px
	.action-page
		section.content
			.section-head
				font-size: 30px

		.main-content
			img
				float: none !important
				width: 100%
				max-height: 150px
				object-fit: cover
				margin-right: 0
	.service-page
		.services-list
			.service-wrap
				margin-bottom: 20px
	.about
		.director
			padding: 30px

@media (max-width: 576px)
	.main
		section
			&.locations
				.locations
					&-slide
						padding: 20px 10px
	.about section.history .history-block
		padding: 0 0 15px

@media (max-width: 553px)
	.actions
		section.actions
			.filter
				&-wrap
					flex-direction: column
					align-items: flex-start

				&-items
					margin-top: 10px
					padding-left: 0
	.action-page
		.main-content
			img
				margin-right: 0

@media (max-width: 500px)
	.section-head
		font-size: 43px
	.section-sub-head
		font-size: 30px
	.main
		section
			&.services
				.section-head
					margin-bottom: 20px
	.not-found-wrap
		.section-sub-head
			font-size: 40px
			line-height: normal

@media (max-width: 370px)
	.not-found-wrap
		.section-head
			font-size: 150px
