.contacts-page
    background-image: url('../images/map-bg.png')
    background-position: center
    background-repeat: no-repeat
    background-size: cover
    .contacts
        background-image: none
        &-section-wrap
            padding-top: 50px
    .section-head-dark span
        color: #ffffff

.contacts
    background-image: url('../images/map-bg.png')
    font-family: 'Trebuchet MS', 'Roboto', sans-serif
    background-repeat: no-repeat
    background-position: center center
    background-size: cover
    padding: 150px 0

    &-section-wrap
        .section-head
            margin-bottom: 85px
    p
        margin-bottom: 0 !important
    .section-head
        margin-bottom: 60px
    .section-sub-head
        margin-bottom: 33px !important
        span
            color: color(green)
    .social
        display: flex
        align-self: flex-end
        margin-top: 30px
        gap: 42px 42px
        a
            font-size: 23px
            &:hover
                color: color(green) !important

    &-wrap
        background:
            image: url('../images/contacts-waves-bg.png')
            repeat: no-repeat
            position: right top
            size: contain
            color: #fff
        padding: 96px 52px 43px 72px
    &-list
        &-item
            position: relative
            padding-left: 30px
            &:not(:last-child)
                margin-bottom: 20px
            img
                position: absolute
                top: 6px
                left: 0
                width: 20px
                height: 20px
            &-text
                color: color(sub-text)
                font-size: 18px
                line-height: 32px
                > *
                    display: block
                    width: fit-content
                a:hover
                    color: color(green)
