.service-page
	+dark-header
	.section-head
		&:not(.main)
			font-size: 50px
			line-height: 58px
	.head
		background-image: url('../images/service-head-bg.png')
		background-repeat: no-repeat
		background-position: top right
		.section-head
			margin-bottom: 82px
	.services-head
		background-color: color(green)
		padding: 0
		.container
			padding-top: 112px
			padding-bottom: 111px
			background-image: url('../images/services-waves-bg.png')
			background-repeat: no-repeat
			background-position: left bottom
	.galery
		background-image: url('../images/galery-waves-bg.png')
		background-position: left center
		background-repeat: no-repeat
		.section-head
			margin-bottom: 80px
		img
			height: 328px
			display: block
			object-fit: cover
			margin-bottom: 32px
.services-slider
	.slick-dots
		li
			button
				background-color: color(bg)
			&.slick-active
				button
					background-color: color(green)
.services-list
	.service
		&-wrap
			margin-bottom: 85px
			.btn
				width: 100%
		&-img
			width: 100%
			&-wrap
				position: relative
				margin-bottom: 38px
		&-icon
			position: absolute
			top: 0
			left: 19px
			transform: translateY(-50%)
			z-index: 1
			width: fit-content
			padding: 12px
			border-radius: 16px
			background-color: #ffffff
			box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1)
			img
				display: block
				width: 40px
				height: 40px
		&-name
			color: color(green)
			font-size: 26px
			line-height: 30px
			margin-bottom: 55px
		&-description
			font-size: 18px
			line-height: 32px
			color: color(sub-text)
