//Roboto Font
@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;700;900&display=swap')

.btn
	display: block
	width: fit-content
	padding: 15px
	border: 1px solid color(bg)
	border-radius: 24px
	text-transform: uppercase
	font-family: 'Roboto', sans-serif
	font-size: 16px
	font-weight: bold
	text-align: center
	+split-colors-on-hover(#fff, color(bg))

.progress
	background-color: rgba(255, 255, 255, 0.2)
	&.horizontal
		height: 8px
		.current
			height: 8px

	.current
		background-color: color(green)
		transition: .5s

.section
	&-sub-head
		font-size: 36px
		line-height: 42px
		font-weight: bold
		color: color(green)

		&.light
			color: #ffffff
		&.dark
			color: color(text)

	&-head
		font-weight: bold
		font-size: 100px
		line-height: 116px
		color: color(green)

		span
			color: #fff

		&-dark
			span
				color: color(text)

		&.light
			color: #ffffff

			&-green
				span
					color: color(green)
	&-teaser
		font-size: 18px
		line-height: 32px
		color: color(sub-text)

		> *:not(:last-child)
			margin-bottom: 32px

section
	padding: 145px 0
	&.breadcrumbs
		padding: 207px 0 130px

.youtube
	position: relative
	margin: 32px 0
	&:not(.video-loaded)
		cursor: pointer
		&::before
			content: '\f167'
			font-family: "Font Awesome 5 Brands"
			font-weight: 400
			font-size: 50px
			color: #f00
			z-index: 2
		&::after
			content: ''
			background-color: #fff
			width: 39px
			height: 34px
			z-index: 1
		&::after,
		&::before
			position: absolute
			top: 50%
			left: 50%
			transform: translate(-50%, -50%)
	img
		aspect-ratio: 16/9
		width: 100%
	iframe
		position: absolute
		top: 0
		left: 0
		bottom: 0
		right: 0
		width: 100%
		height: 100%

