@use 'sass:math'
@import url('https://fonts.googleapis.com/css2?family=Marck+Script&display=swap')

.about
    section.breadcrumbs
        padding-bottom: 30px
    .head
        background-image: url('../images/about-waves-bg.png')
        background-repeat: no-repeat
        background-position: left 420px
        .section-head
            margin-bottom: 85px

    .director
        text-align: center
        padding: 50px 80px
        background: #FFFFFF
        box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.1)

        &-name
            font-size: 36px
            line-height: 42px
            color: #F49A05
            margin-bottom: 30px

        &-photo
            margin: 0 auto 20px

        &-position
            font-size: 30px
            line-height: 35px
            margin-bottom: 20px

        &-signature
            color: color(green)
            font-family: 'Marck Script', cursive
            font-size: 36px
            line-height: 45px
            letter-spacing: -2.305px
            text-indent: 2px
            img
                margin: 0 auto
                max-height: 100px

        &-description
            font-size: 18px
            line-height: 32px

    .about-head
        background-color: color(green)
        padding: 0

        .container
            padding-top: 112px !important
            padding-bottom: 111px !important
            background-image: url('../images/services-waves-bg.png')
            background-repeat: no-repeat
            background-position: left bottom

    section
        &.history
            padding-top: 119px
            padding-bottom: 196px

            .history-slider-year
                position: relative
                &:after
                    content: ''
                    position: absolute
                    bottom: 0
                    left: 40px
                    right: 40px
                    background-color: #d8d8d8
                    opacity: .2
                    height: 13px
                .slick-track
                    align-items: flex-end
                    min-height: 70px
                .slick-current.year
                    color: color(bg)
                    font-size: 36px
                    line-height: 42px
                    &:after
                        background-color: color(bg)
                .year
                    font-weight: bold
                    font-size: 20px
                    line-height: 23px
                    flex-wrap: wrap
                    flex-direction: column
                    align-items: center
                    color: #b9c7a1
                    transition: .5s
                    &:after
                        content: ''
                        margin-top: 15px
                        display: block
                        width: 13px
                        height: 13px
                        background-color: #b9c7a1
                        transition: .5s

            .history
                &-arrows
                    display: flex
                    justify-content: center
                    margin-bottom: 38px
                    .slick-arrow
                        transform: none
                        position: unset
                        color: color(sub-text)
                        transition: .5s
                        &:hover
                            color: color(green)
                        &:not(:last-child)
                            margin-right: 30px

                &-block
                    padding: 0 math.div(62px, 2) 56px

                    &-date
                        font-weight: bold
                        font-size: 38px
                        line-height: 44px
                        text-align: center
                        color: color(text)
                        margin-bottom: 74px

                    &-sub-head
                        font-size: 30px
                        line-height: 35px
                        color: color(green)
                        margin-bottom: 32px
                        text-align: center

                    &-text
                        font-size: 18px
                        line-height: 32px
                        color: color(sub-text)

        &.team
            .container-custom
                &:not(:last-child)
                    margin-bottom: 120px
