.news-subscribe
    padding: 38px 69px
    background: #FFFFFF
    box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.1)
    @media (max-width: 1199px)
        padding: 30px
    @media (max-width: 500px)
        padding: 15px

    .news-categories
        display: flex
        flex-wrap: wrap
        justify-content: flex-start
        padding: 0 25px
        margin-bottom: 50px
        font-size: 20px
        line-height: 23px
        font-weight: bold
        gap: 7px 7px
        @media (max-width: 1199px)
            padding: 0
            margin-bottom: 15px

        span,
        a
            font-weight: normal
            color: #90D21F !important
            text-decoration: underline #90D21F
            transition: .5s
            cursor: pointer
            display: inline-block

            &:hover
                text-decoration: underline transparent

    &-head
        font-size: 28px
        line-height: 33px
        font-weight: bold
        color: #262323
        margin-bottom: 30px

        span
            color: #f49a05

    &-form
        margin-bottom: 40px

        .v-text-field
            margin-bottom: 26px

            &:last-of-type
                margin-bottom: 50px

            .v-label
                color: #B9C7A1
                font-size: 18px

            .v-input__slot:before
                border-color: #B9C7A1 !important

    &-social
        p
            font-size: 18px
            line-height: 21px
            color: #929292
            font-weight: bold
            margin-bottom: 30px

        .social
            display: flex
            align-content: center
            justify-content: center

            a
                transition: .5s
                font-size: 23px
                &:not(:last-child)
                    margin-right: 50px
                &:hover
                    color: #B9C7A1 !important
