.locations-page
    .head
        background-image: url('../images/locations-waves-bg.png')
        background-repeat: no-repeat
        background-position: right 420px
        .section-head
            margin-bottom: 90px

        .location
            &-content-wrap
                &:not(:last-child)
                    margin-bottom: 90px
                &:nth-child(odd)
                    .row
                        flex-direction: row-reverse

            &-image-slider
                height: 100%

                .slick
                    &-prev,
                    &-next
                        width: 76px
                        height: 76px
                        background-color: rgba(255, 255, 255, .32)
                        padding: 0
                        font-size: 0
                        color: #000000
                        &:hover
                            color: color(green)
                        &:before
                            font-family: "Font Awesome 5 Free"
                            font-weight: 900
                            font-size: 36px
                    &-prev
                        &:before
                            content: '\f104'
                    &-next
                        &:before
                            content: '\f105'
                    &-list,
                    &-track
                        height: 100%

                    &-slide
                        *
                            height: 100%
                            cursor: default

                img
                    object-fit: cover

            &-wrap
                padding: 82px 41px 33px 65px
                background: #fff
                box-shadow: 0 2px 40px rgba(0, 0, 0, 0.1)
                height: 100%
                display: flex
                flex-wrap: wrap
                align-items: center

            &-name
                font-weight: bold
                font-size: 34px
                margin-bottom: 38px

                span
                    color: color(green)
                    font-size: 24px
                    margin-right: 5px

            &-description
                font-size: 18px
                line-height: 32px
                color: color(text-light)
                margin-bottom: 40px

            &-features
                display: flex
                flex-wrap: wrap
                align-items: center
                gap: 20px 20px

            &-feature
                margin-bottom: 10px
                max-width: 128px

                &-icon
                    width: fit-content
                    margin: 0 auto 4px
                    padding: 11px
                    background-color: #ffffff
                    box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1)

                    img
                        display: block
                        width: 40px
                        height: 42px

                &-name
                    font-size: 18px
                    line-height: 32px
                    color: color(bg)
                    text-align: center
