.main
	header
		&:not(.dark)
			background-color: transparent
	section.head
		padding: 0

	.head
		background: rgba(30, 31, 32, 0.81)
		&-slider

			&-progress
				position: absolute
				top: 50%
				left: 34px
				transition: .5s
				transform: translateY(-50%)
				display: flex
				flex-direction: column
				align-items: center
				color: #ffffff

				.progress
					margin: 8px 0
					height: 54vh
					width: 2px

					.current
						width: 100%
						height: 0

		&-slide
			position: relative
			display: flex
			min-height: 100vh
			background-position: center
			background-size: cover
			background-repeat: no-repeat
			align-items: flex-end
			color: #ffffff
			padding: 150px 0 300px
			&:before
				content: ''
				position: absolute
				top: 0
				right: 0
				bottom: 0
				left: 0
				background-color: rgba(#181620, 0.3)

			&-head
				font-size: 80px
				text-transform: uppercase
				line-height: 93px
				font-weight: bold

				span
					color: color(green)

			&-sub-head
				font-size: 50px
				line-height: 58px

	.with-support-slider
		display: flex
		flex-wrap: wrap
		justify-content: space-between
		align-items: center
		height: 136px
		>*
			max-width: 186px
		img
			object-fit: contain
		&-wrap
			color: #ffffff
			position: absolute
			bottom: 16px
			width: 100%

			p
				margin-bottom: 32px

	.about
		&-arrows
			margin-top: 60px

			.slick-arrow
				position: unset
				font-size: 18px

				&:not(:last-child)
					margin-right: 10px

				&:hover
					color: color(green)

		&-slider
			margin-top: 90px

			&-progress
				position: absolute
				bottom: 0
				width: 100%

				.progress

					.current
						width: 0
						height: 8px

		&-slide
			opacity: .4
			filter: grayscale(1)

			&.slick-current
				opacity: 1
				filter: grayscale(0)

			img
				margin-bottom: 24px

			&-head
				font-weight: bold
				font-size: 24px
				line-height: 28px
				margin-bottom: 28px

				span
					color: color(green)
					font-size: 20px
					margin-right: 5px

			&-text
				font-size: 18px
				line-height: 32px
				color: color(text-light)

		&-image
			&-slide
				object-fit: cover

			&-slider
				width: 56vw
				max-width: 900px

				&-main-wrap
					position: absolute
					top: 0
					right: 42px
					z-index: 1

				&-wrap
					position: relative

	section
		&.subscribe
			background-color: color(green)
			background-image: url('../images/home/waves.png')
			background-repeat: no-repeat
			background-position: right center
			background-size: cover

			.btn
				margin-top: 24px
				padding-right: 40px
				padding-left: 40px

		&.waves-bg
			background-image: url('../images/home/waves-bg.png')
			background-repeat: no-repeat
			background-position: 0 0
			background-size: 100%

		&.services
			padding-bottom: 76px

			.btn.block-center
				margin-top: 80px

			.section-head
				margin-bottom: 64px

			.service
				margin-bottom: 20px
				box-shadow: 0 2px 20px rgba(0, 0, 0, 0.1)
				border-radius: 0 0 5px 5px

				&-img
					display: block
					width: 100%
					height: 250px
					object-fit: cover
					transition: .5s
					border-radius: 5px 5px 0 0

					&-wrap
						position: relative

						&:before
							content: ''
							border-radius: 5px 5px 0 0
							position: absolute
							top: 0
							left: 0
							width: 100%
							height: 100%
							background-color: transparent
							transition: .5s

				&-icon
					opacity: 1
					transition: .2s
					position: absolute
					bottom: 0
					left: 32px
					transform: translateY(50%)
					z-index: 1
					width: fit-content
					padding: 12px
					border-radius: 16px
					background-color: #ffffff
					box-shadow: 0px 2px 20px rgba(0, 0, 0, 0.1)
					img
						height: 40px
						width: 40px

				&-name
					font-size: 26px
					line-height: 30px
					color: color(text)
					transition: .5s
					transform: none

				&-description
					transition: .5s
					padding: 48px 5px 48px 32px
					background-color: #ffffff
					border-radius: 0 0 5px 5px

					&-wrap
						height: 0
						overflow: hidden
						transition: .5s

					&-text
						font-size: 18px
						line-height: 32px
						color: color(sub-text)
						margin-bottom: 28px

						span
							color: color(green)

				&:hover
					.service
						&-icon
							opacity: 0

						&-img
							object-position: bottom
							height: 125px

							&-wrap
								&:before
									background-color: rgba(color(green), .5)

						&-name
							transform: translateY(-230%)
							color: #ffffff
							margin-bottom: -30px

						&-description
							padding-top: 25px
							padding-bottom: 20px
							&-wrap
								height: 207px

				.btn
					i
						margin-left: 10px

		&.locations
			padding-top: 76px
			.section-head
				margin-bottom: 84px

			.locations
				&-image-slide
					object-fit: cover
					transition: .5s

				&-image-slider
					&-main-wrap
						position: absolute
						top: 0
						left: 0
						right: 0

					&-wrap
						position: relative

						.locations-slider-progress
							position: absolute
							bottom: 0
							left: 0
							right: 0
							z-index: 0

				&-slider
					box-shadow: 0px 2px 40px rgba(0, 0, 0, 0.1)

					.slick-arrow
						top: unset
						bottom: 20px
						color: color(sub-text)
						font-size: 20px

						&:hover
							color: color(green)

						&:nth-of-type(1)
							left: unset
							right: 68px

						&:nth-of-type(2)
							right: 27px

				&-slide
					background-color: #ffffff
					border-radius: 2px
					padding: 73px 120px 73px 73px

					&-name
						font-size: 34px
						font-weight: bold
						margin-bottom: 38px

						span
							color: color(green)
							font-size: 24px
							margin-right: 5px

					&-text
						font-size: 18px
						line-height: 32px
						margin-bottom: 31px
						color: color(sub-text)

					.btn
						padding-right: 30px
						padding-left: 30px

						i
							margin-left: 10px
