*, ::after, ::before
  box-sizing: border-box
  outline: none !important

*::selection
  color: #fff
  background-color: color(bg)

html, body
  width: 100%
  height: 100%


body
  font-family: 'Trebuchet MS', 'Roboto', sans-serif
  font-weight: 300
  margin: 0 auto
  cursor: default
  position: relative
  &.modal-open
    overflow: hidden
    padding-right: 17px


a, a:active, a:hover, a:focus
  text-decoration: none
  color: inherit
  transition: .5s


h1, h2, h3, h4, h5, h6
  font-size: inherit
  font-weight: inherit
  letter-spacing: normal
  color: inherit
  margin-bottom: 0
  margin-top: 0
  font-family: inherit


$sizes: (1: 0, 2: 0.5, 3: 0.75, 4: 1, 5: 1.25, 6: 1.5)

@each $i, $size in $sizes
  h#{$i},
  .h#{$i}
    font-size: ( 2.5 - $size ) * 1em

p
  margin: 0

img
  width: auto
  height: auto
  max-width: 100% !important
  object-fit: contain
  display: block

button
  border: none
  background-color: transparent
  cursor: pointer
  transition: .5s
  font-size: inherit
  margin: 0
  padding: 0

input, button
  padding-top: 5px
  padding-bottom: 5px
  font-family: inherit

ul
  &.unmarked
    list-style: none

  &.inline
    margin: 0
    padding: 0

    li
      display: inline-block

.text
  &-right
    text-align: right
  &-center
    text-align: center
  &-left
    text-align: left

.block
  &-center
    margin-left: auto
    margin-right: auto
  &-left
    margin-right: auto
  &-right
    margin-left: auto
