header
	position: fixed
	top: 0
	width: 100%
	z-index: 100
	background-color: rgba(#181620, .3)
	color: #ffffff
	padding: 48px 0
	transition: .5s
	.open-menu
		margin-left: auto
		height: 46px
		display: flex
		align-items: center
		justify-content: center
		&:hover
			background-color: color(bg)
			color: #ffffff
	&.small
		padding: 10px 0
	&.dark
		background-color: #181620
	.logo
		font-weight: 900
		font-size: 32px
		img
			width: 160px
	ul
		list-style: none
		display: flex
		flex-wrap: wrap
		margin: 0
		padding: 0
		li
			position: relative

			&:hover
				ul
					opacity: 1
					visibility: visible

			ul
				transition: all 250ms
				opacity: 0
				visibility: hidden
				position: absolute
				top: 100%
				left: 50%
				transform: translateX(-50%)
				padding: 16px 0

				li
					padding-bottom: 10px
					background-color: rgba(#000, 0.5)
					text-align: center
					min-width: 320px

					a
						display: block
						padding: 0 10px

		&.social
			justify-content: center
			font-size: 18px
			a
				position: relative
				padding: 17px 20px
				display: flex
				justify-content: center
				align-items: center
				&:before
					content: ''
					position: absolute
					top: 0
					right: 0
					width: 100%
					height: 100%
					background-image: url('../images/social-hover.png')
					background-repeat: no-repeat
					background-position: center
					background-size: contain
					opacity: 0
					transition: .5s
				&:hover
					&:before
						opacity: 1
	nav
		ul
			justify-content: space-between
			font-family: 'Trebuchet MS', 'Roboto', sans-serif
			li
				font-size: 24px
				transition: none
				&.active,
				&:hover
					> a
						color: color(bg)
	.subscribe
		width: 100%
	.mobile-menu
		position: fixed
		top: 0
		width: 236px
		height: 100vh
		z-index: 101
		padding: 8px 16px
		background-color: #fff
		color: #000
		transform: translateX(-100%)
		transition: .5s
		&.open
			transform: translateX(0px)
		> *
			&:not(:last-child)
				margin-bottom: 10px
		nav
			ul
				flex-direction: column
				li
					font-size: 18px
					a
						display: flex
						align-items: center
						//padding: 0 16px
						min-height: 48px
		.social
			justify-content: flex-start
