.not-found
	+dark-header
	&-wrap
		padding-top: 125px + 150px
		background-repeat: no-repeat
		background-position: right top
		background-image: url('../images/not-found-waves-bg.png')
		.section-head
			font-size: 300px
			line-height: 348px
			margin-bottom: 80px
		.section-sub-head
			color: color(bg)
			font-size: 50px
			line-height: 58px
			margin-bottom: 150px
		.head
			margin-bottom: 30px
			font-size: 24px
			line-height: 28px
			font-weight: bold
			color: color(text)
		.menu
			list-style: none
			margin: 0
			padding: 0
			li
				font-size: 18px
				line-height: 16px
				color: color(sub-text)
				&:hover
					color: color(bg)
				&:not(:last-child)
					margin-bottom: 15px
		.reasons
			list-style: none
			margin: 0
			padding-left: 16px
			li
				position: relative
				font-size: 18px
				line-height: 21px
				color: color(sub-text)
				&:before
					content: ''
					position: absolute
					top: 9px
					left: -16px
					width: 7px
					height: 7px
					background-color: color(bg)
					border-radius: 50%
				&:not(:last-child)
					margin-bottom: 20px
